<template>
    <div class="result-wrapper">
        <div class="card light result mt-20">
            <div class="card-header">
                <div class="card-title summary">거래내역 <span>{{countsOfTras}}</span>건</div>
                <JsonExcel v-if="logStatus.downEx" class="btn outline btn-excel" :data="resultData" :fields="traFields" :name="dnExcelName" :beforeGenerate="setDownloadName" :stringifyLongNum=true><i class="fas fa-download"></i>&nbsp;</JsonExcel>
                <button v-else type="button" class="btn outline btn-excel" @click="auth4ExcelDownload"><i class="fas fa-download"></i>&nbsp;</button>
            </div>
            <div class="card-body">
                <table v-if="countsOfTras>0">
                    <tr>
                        <th>결제건명</th>
                        <th>이용자명</th>
                        <th>결제금액</th>
                        <th>결제수단</th>
                        <th>주문번호</th>
                        <th>결제일시</th>
                        <th></th>
                    </tr>
                    <!-- <tr v-for='item in resultData' :key='item.tid' @click="auth4ViewDetail(item.tid)" class="hoverable"> -->
                    <tr v-for='item in resultData' :key='item.tid' class="hoverable">
                        <td>{{item.productName}}</td>
                        <td @click="auth4ViewDetail(item.tid)">{{item.name_m}}</td>
                        <td>{{item.paymentAmount}}</td>
                        <td>{{item.bankName}}</td>
                        <td @click="auth4ViewDetail(item.tid)">{{item.orderNo.slice(-9).toUpperCase()}}</td>
                        <!-- <td></td> -->
                        <td @click="auth4ViewDetail(item.tid)">{{item.adate}}</td>
                        <td @click="auth4ViewDetail(item.tid)"><i class="fas fa-angle-right"></i></td>
                    </tr>
                </table>
                <!--<ul v-if="countsOfTras>0" class="u-table">
                    <li class="table-row">
                        <div class="t-header">결제건명</div>
                        <div class="t-header">이용자명</div>
                        <div class="t-header"></div>
                        <div class="t-header"></div>
                        <div class="t-header"></div>
                        <div class="t-header"></div>
                    </li>
                    <li class="table-row">
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                        <div class="t-data"></div>
                    </li>
                </ul>-->
                <p v-else-if="countsOfTras==0">해당하는 결과가 없습니다.</p>
                <p v-else>결과를 불러오지 못했습니다.</p>
            </div><!-- card-body-->
        </div>   

        <form action="" v-on:submit.prevent="submitForm">
            <!-- 컴포넌트 연동-->
            <Modal v-show="isVisible" @close="closeModal">
                <div class="card-title" slot="header">
                    사용자 확인
                </div>
                <template slot="body">
                    <p class="guide-text">{{ guideText }}</p>
                    <div>
                        <input type="password" id="modal_pw" v-model="passwd"/>
                    </div>
                </template>
                <template slot="footer">
                    <button type="submit" class="block primary">확인</button>
                </template>
            </Modal>
        </form>
        
        <!--
        <div class="modal mask" id="modal_auth">
            <div class="modal-content" id="card_auth">
                <div class="card light">
                    <div class="card-header">
                        <div class="card-title">사용자 확인</div>
                    </div>
                    <div class="card-body">
                        <div class="container-fluid">
                            <p class="guide-text">{{ guideText }}</p>
                            <form action="" v-on:submit.prevent="submitForm">
                                <div>
                                    <input type="password" id="modal_pw" v-model="passwd"/>
                                </div>
                                <button type="submit" class="block primary">확인</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>-->

    </div>

</template>
<script>
//import { mapGetters } from 'vuex'
import JsonExcel from 'vue-json-excel'
import Modal from './Modal.vue'
import api from '@/utils/api'

export default {
    name:'ResultTras',
    components: {
        JsonExcel,
        Modal,
    },
    data() {
        return {
            isVisible:false,
            guideText:'',
            passwd:'',
            curTid:'',
            dnExcelName:'list.xls',
            traFields: {
                '결제일시':'adate',
                '결제건명': 'productName',
                //'이용자명': 'name_m',
                '이용자명': 'payerName',
                'Email': 'email',
                '전화번호': 'u_phone',
                '결제금액': 'paymentAmount',
                '결제기관': 'bankName',
                '거래상태': 'tstatus',
                '취소일시': 'canceledTimestamp',
            }
        }
    },
    computed:{
        /*countsOfTras() {
            return this.results.length;
        },
        results() {
            return this.$store.getters.tras;
        }*/
        // 위 처리 모듈을 mapGetters로 통합 => SearchTras로 이동
        // ...mapGetters({
        //   countsOfTras:'getCounts',
        //   results:'tras'  
        // })
        countsOfTras() {
            return this.$parent.countsOfTras
        },
        resultData() {
            return this.$parent.results
        },
        authorized4Det() {
            return this.$parent.reAuthorized.auth
        },
        logStatus() {
            return {
                purpose: this.$parent.reAuthorized.purpose,
                downEx: this.$parent.reAuthorized.downEx,
                viewDet: this.$parent.reAuthorized.viewDet,
            }
        }
    },
    methods:{
        async submitForm() {
            let reAuth = this.logStatus
            // 패스워드 인증 후 오픈
            try {
                const response = await api.login({ userid:localStorage.getItem('instapay_id'), password:this.passwd })

                if(response.data.result === 'ok') {
                    localStorage.setItem('instapay_token', response.data.token)
                    api.updateToken()
                    console.log('instapay_token', response.data.token)
                    if(reAuth.purpose=='viewDet') {
                        reAuth.viewDet = true
                        this.isVisible = false
                        this.viewTras(this.curTid)
                    }
                    else {
                        reAuth.downEx = true
                        this.isVisible = false
                    }
                    api.writeLogPurpose('transaction list', reAuth);
                    
                    // update login status
                    reAuth.auth = true
                    this.$emit('authorized',reAuth)
                }
            } catch (error) {
                console.log(error.message)
                reAuth.auth = false
                this.$emit('authorized',reAuth)
            }
        },
        auth4ViewDetail(tid) {
            let reStat = this.logStatus
            reStat.purpose = 'viewDet'

            if(this.authorized4Det) {
                if(!reStat.viewDet) {
                    api.writeLogPurpose('transaction list', reStat);
                    reStat.viewDet = true
                }
                this.$emit('authorized',{ auth:true, ...reStat })
                this.viewTras(tid)
                return
            }
            this.guideText = "로그인 비밀번호를 입력해 주십시오.";
            this.curTid = tid;
            this.isVisible = true;
            /*
            var winH = window.innerHeight;
            const mAuth = document.getElementById('modal_auth');
            const mCon = document.getElementById('card_auth');
            mAuth.style.display = 'block';
            mCon.style.top = (winH-mCon.offsetHeight)/2 + 'px';*/
            // submitForm을 호출해야 함
        },
        auth4ExcelDownload() {
            let reStat = this.logStatus
            reStat.purpose = 'downExcel'
            // update purpose
            this.$emit('authorized',{ auth:this.authorized4Det, ...reStat })

            // downEx status가 false인 상황에서는 무조건 다시 로그인 
            this.guideText = "로그인 비밀번호를 입력하여 로그인 후 다시 다운로드 버튼을 눌러 주십시오."
            this.isVisible = true
        },
        closeModal() {
            this.isVisible = false;
        },
        viewTras(tid) {
            /* Alt) path의 param으로 tid를 전달하는 방식
            this.$router.push('/tras/'+tid);

            // 선택한 item(거래상세데이터)를 event payload로 상세 페이지에 전달하는 방식(non-Vuex)
            const item = this.results.tras.find(tra=>tra.tid == tid);
            console.log(item);
            this.$emit('openDet',item); */

            // vuex store에 선택된 아이템(tid)을 저장
            //console.log(tid);
            this.$router.push('/tras/detail');
            // vuex mutation commit을 통해 현재 열릴 tid를 저장 
            this.$store.commit('setCurTid', tid);   
            //sessionStorage.setItem('curTid',tid);
        },
        setDownloadName(){
            this.dnExcelName = 'list'+(new Date()).toISOString()+'.xls' 
        }
    }
}
</script>

<style scoped>
table { width:100%; border-collapse:collapse; table-layout:fixed; }
th, td { text-align:left; padding:0.25rem 0.5rem; overflow:hidden; white-space:nowrap;text-overflow:ellipsis; }

tr.hoverable:hover { /*cursor:pointer;*/ background-color:var(--bg-lightgrey); }
tr.hoverable > td:nth-child(2):hover { cursor:pointer }
tr.hoverable > td:nth-child(6):hover { cursor:pointer }
tr.hoverable > td:nth-child(7):hover { cursor:pointer }
tr.hoverable > td:nth-child(5):hover { cursor:pointer }

.btn-excel { color:var(--primary) }
button.btn-excel { color:var(--text-dark) }

.card.result table>tr>th:first-child, .card.result table>tr>td:first-child { width:40%;}
.card.result table>tr>th:nth-child(3) { width:80px;}
.card.result table>tr>th:nth-last-child(2) { width:150px;}
.card.result table>tr>th:last-child { width:26px;}

/* for Responsive Table */
/* @media (max-width:767px) {
#resultTable table.rs > tr > td:nth-of-type(1):before { font-weight:bold; content: "결제건명"; }
#resultTable table.rs > tr > td:nth-of-type(2):before { font-weight:bold; content: "이용자명"; }
#resultTable table.rs > tr > td:nth-of-type(3):before { font-weight:bold; content: "결제금액"; }
#resultTable table.rs > tr > td:nth-of-type(4):before { font-weight:bold; content: "결제기관"; }
#resultTable table.rs > tr > td:nth-of-type(5):before { font-weight:bold; content: "주문번호"; }
#resultTable table.rs > tr > td:nth-of-type(6):before { font-weight:bold; content: "결제일시"; }

#resultTable table.rs > tr > td:nth-of-type(7) { display:none; }
}*/

#modal_pw { 
		display: block;
		width: 100%;
		padding: 6px 12px;
		font-size: 14px;
		height: 36px;
		margin-bottom: 0.5rem;}


</style>